.about-content {
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 60px;
  align-items: center;
}

.about-text .section-header {
  text-align: left;
  margin-bottom: 30px;
}

.about-text p {
  font-size: 1.1rem;
  line-height: 1.8;
  color: var(--secondary-color);
  margin-bottom: 25px;
}

.about-buttons {
  display: flex;
  gap: 20px;
  flex-wrap: wrap;
}

.about-buttons .btn {
  padding: 15px 35px;
  font-size: 1.1rem;
  border-radius: 50px;
}

.about-image {
  display: flex;
  justify-content: center;
  align-items: center;
}

.about-image .about-photo {
  width: 400px;
  height: 400px;
  border-radius: 20px;
  object-fit: cover;
  border: 4px solid var(--primary-color);
  box-shadow: 0 20px 40px rgba(0, 0, 0, 0.15);
  transition: transform 0.3s ease, box-shadow 0.3s ease;
}

.about-image .about-photo:hover {
  transform: scale(1.02);
  box-shadow: 0 30px 60px rgba(0, 0, 0, 0.25);
}

.about-image .image-placeholder {
  width: 400px;
  height: 400px;
  border-radius: 20px;
  background-color: var(--light-color);
  border: 4px solid var(--primary-color);
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  box-shadow: 0 20px 40px rgba(0, 0, 0, 0.15);
}

.about-image .image-placeholder i {
  font-size: 4rem;
  color: var(--primary-color);
  margin-bottom: 15px;
}

.about-image .image-placeholder p {
  color: var(--secondary-color);
  font-size: 1.1rem;
}

@media (max-width: 768px) {
  .about-content {
    grid-template-columns: 1fr;
    gap: 40px;
    text-align: center;
  }
  
  .about-text .section-header {
    text-align: center;
  }
  
  .about-buttons {
    justify-content: center;
  }
  
  .about-image .about-photo,
  .about-image .image-placeholder {
    width: 300px;
    height: 300px;
  }
}
