.blog-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(350px, 1fr));
  gap: 40px;
}

.blog-item {
  background: var(--card-background);
  border-radius: 15px;
  overflow: hidden;
  box-shadow: var(--shadow);
  transition: var(--transition);
  border: 1px solid var(--border-color);
}

.blog-item:hover {
  transform: translateY(-8px);
  box-shadow: 0 20px 40px rgba(0, 0, 0, 0.15);
  border-color: var(--primary-color);
}

.blog-image {
  height: 250px;
  overflow: hidden;
  position: relative;
}

.blog-image .blog-photo {
  width: 100%;
  height: 100%;
  object-fit: cover;
  transition: transform 0.3s ease;
}

.blog-item:hover .blog-photo {
  transform: scale(1.05);
}

.blog-image .image-placeholder {
  height: 100%;
  border: none;
  background-color: var(--light-color);
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.blog-image .image-placeholder i {
  font-size: 3rem;
  color: var(--primary-color);
  margin-bottom: 15px;
}

.blog-image .image-placeholder p {
  color: var(--secondary-color);
  font-size: 1rem;
}

.blog-content {
  padding: 30px;
}

.blog-content h3 {
  font-size: 1.3rem;
  font-weight: 600;
  color: var(--dark-color);
  margin-bottom: 15px;
  line-height: 1.4;
}

.blog-content p {
  color: var(--secondary-color);
  line-height: 1.6;
  margin-bottom: 20px;
}

.blog-meta {
  display: flex;
  justify-content: space-between;
  align-items: center;
  font-size: 0.9rem;
  color: var(--secondary-color);
}

.blog-meta .date {
  color: var(--primary-color);
  font-weight: 500;
}

.blog-meta .comments {
  background-color: var(--light-color);
  padding: 5px 12px;
  border-radius: 15px;
  font-size: 0.8rem;
  border: 1px solid var(--border-color);
}

@media (max-width: 768px) {
  .blog-grid {
    grid-template-columns: 1fr;
    gap: 30px;
  }
  
  .blog-content {
    padding: 25px;
  }
}
