.hero {
  background: linear-gradient(135deg, #000000 0%, #333333 50%, #666666 100%);
  color: white;
  padding: 120px 0 80px;
  min-height: 100vh;
  display: flex;
  align-items: center;
  position: relative;
  margin-top: 0;
}

[data-theme="dark"] .hero {
  background: linear-gradient(135deg, #1a1a1a 0%, #2d2d2d 50%, #404040 100%);
}

.hero::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: linear-gradient(45deg, rgba(0,0,0,0.7) 0%, rgba(255,255,255,0.1) 100%);
  z-index: 1;
}

[data-theme="dark"] .hero::before {
  background: linear-gradient(45deg, rgba(0,0,0,0.8) 0%, rgba(255,255,255,0.05) 100%);
}

.hero-content {
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 60px;
  align-items: center;
  position: relative;
  z-index: 2;
}

.hero-text h1 {
  font-size: 3.5rem;
  font-weight: 700;
  margin-bottom: 20px;
  line-height: 1.2;
}

.hero-text h1 strong {
  color: #ffffff;
  text-shadow: 2px 2px 4px rgba(0,0,0,0.5);
}

.hero-text h2 {
  font-size: 2rem;
  font-weight: 400;
  margin-bottom: 30px;
  color: #f0f0f0;
}

.hero-text p {
  font-size: 1.2rem;
  margin-bottom: 40px;
  line-height: 1.8;
  color: #e0e0e0;
}

.hero-buttons {
  display: flex;
  gap: 20px;
  flex-wrap: wrap;
}

.hero-buttons .btn {
  padding: 15px 35px;
  font-size: 1.1rem;
  border-radius: 50px;
}

.hero-image {
  display: flex;
  justify-content: center;
  align-items: center;
}

.hero-image .profile-photo {
  width: 400px;
  height: 400px;
  border-radius: 50%;
  object-fit: cover;
  border: 4px solid rgba(255, 255, 255, 0.3);
  box-shadow: 0 20px 40px rgba(0, 0, 0, 0.4);
  transition: transform 0.3s ease, box-shadow 0.3s ease;
}

.hero-image .profile-photo:hover {
  transform: scale(1.05);
  box-shadow: 0 30px 60px rgba(0, 0, 0, 0.6);
}

.hero-image .image-placeholder {
  width: 400px;
  height: 400px;
  border-radius: 50%;
  background-color: rgba(255, 255, 255, 0.1);
  color: white;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  border: 4px solid rgba(255, 255, 255, 0.3);
  box-shadow: 0 20px 40px rgba(0, 0, 0, 0.4);
}

.hero-image .image-placeholder i {
  font-size: 4rem;
  color: rgba(255, 255, 255, 0.8);
  margin-bottom: 15px;
}

.hero-image .image-placeholder p {
  color: rgba(255, 255, 255, 0.8);
  font-size: 1.1rem;
}

@media (max-width: 768px) {
  .hero {
    padding: 100px 0 60px;
  }
  
  .hero-content {
    grid-template-columns: 1fr;
    gap: 40px;
    text-align: center;
  }
  
  .hero-text h1 {
    font-size: 2.5rem;
  }
  
  .hero-text h2 {
    font-size: 1.5rem;
  }
  
  .hero-buttons {
    justify-content: center;
  }
  
  .hero-image .profile-photo,
  .hero-image .image-placeholder {
    width: 300px;
    height: 300px;
  }
}
