.contact-content {
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 60px;
}

.contact-form h3,
.contact-details h3 {
  font-size: 1.8rem;
  font-weight: 600;
  color: var(--dark-color);
  margin-bottom: 30px;
}

.form-group {
  margin-bottom: 25px;
}

.form-group input,
.form-group textarea {
  width: 100%;
  padding: 15px 20px;
  border: 2px solid var(--border-color);
  border-radius: 10px;
  font-size: 1rem;
  font-family: inherit;
  transition: var(--transition);
  background-color: white;
}

.form-group input:focus,
.form-group textarea:focus {
  outline: none;
  border-color: var(--primary-color);
  box-shadow: 0 0 0 3px rgba(0, 0, 0, 0.1);
}

.form-group textarea {
  resize: vertical;
  min-height: 120px;
}

.contact-form .btn {
  width: 100%;
  padding: 15px;
  font-size: 1.1rem;
  border-radius: 10px;
}

.contact-details ul {
  list-style: none;
}

.contact-details li {
  display: flex;
  align-items: center;
  gap: 20px;
  margin-bottom: 25px;
  padding: 20px;
  background-color: var(--light-color);
  border-radius: 10px;
  border: 1px solid var(--border-color);
  transition: var(--transition);
}

.contact-details li:hover {
  border-color: var(--primary-color);
  transform: translateX(5px);
  box-shadow: 0 5px 15px rgba(0, 0, 0, 0.1);
}

.contact-details i {
  font-size: 1.5rem;
  color: var(--primary-color);
  width: 40px;
  text-align: center;
}

.contact-details span {
  color: var(--text-color);
  font-weight: 500;
}

@media (max-width: 768px) {
  .contact-content {
    grid-template-columns: 1fr;
    gap: 40px;
  }
  
  .contact-details li {
    padding: 15px;
  }
}
