.header {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  background-color: transparent;
  backdrop-filter: none;
  z-index: 1000;
  transition: var(--transition);
  border-bottom: 1px solid transparent;
}

.header.scrolled {
  background-color: rgba(255, 255, 255, 0.95);
  backdrop-filter: blur(10px);
  box-shadow: var(--shadow);
  border-bottom: 1px solid var(--border-color);
}

[data-theme="dark"] .header {
  background-color: transparent;
}

[data-theme="dark"] .header.scrolled {
  background-color: rgba(26, 26, 26, 0.95);
}

.header-content {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 20px 0;
}

.logo h2 {
  color: var(--accent-color);
  font-size: 1.8rem;
  font-weight: 700;
  text-shadow: 2px 2px 4px rgba(0, 0, 0, 0.3);
  transition: var(--transition);
}

.header.scrolled .logo h2 {
  color: var(--primary-color);
  text-shadow: none;
}

[data-theme="dark"] .header .logo h2 {
  color: var(--primary-color);
}

.nav-menu ul {
  display: flex;
  list-style: none;
  gap: 20px;
  flex-wrap: wrap;
}

.nav-menu button {
  background: none;
  border: none;
  color: var(--accent-color);
  font-size: 0.9rem;
  font-weight: 500;
  cursor: pointer;
  padding: 8px 0;
  position: relative;
  transition: var(--transition);
  text-shadow: 1px 1px 2px rgba(0, 0, 0, 0.3);
}

.header.scrolled .nav-menu button {
  color: var(--text-color);
  text-shadow: none;
}

[data-theme="dark"] .header .nav-menu button {
  color: #ffffff;
}

.nav-menu button:hover,
.nav-menu button.active {
  color: var(--accent-color);
}

.header.scrolled .nav-menu button:hover,
.header.scrolled .nav-menu button.active {
  color: var(--primary-color);
}

[data-theme="dark"] .header .nav-menu button:hover,
[data-theme="dark"] .header .nav-menu button.active {
  color: #ffffff;
}

.nav-menu button::after {
  content: '';
  position: absolute;
  bottom: 0;
  left: 0;
  width: 0;
  height: 2px;
  background-color: var(--accent-color);
  transition: var(--transition);
}

.header .nav-menu button::after {
  background-color: var(--primary-color);
}

[data-theme="dark"] .header .nav-menu button::after {
  background-color: #ffffff;
}

.nav-menu button:hover::after,
.nav-menu button.active::after {
  width: 100%;
}

.theme-toggle {
  margin-left: 20px;
}

.theme-toggle-btn {
  background: none;
  border: 2px solid var(--accent-color);
  color: var(--accent-color);
  width: 45px;
  height: 45px;
  border-radius: 50%;
  cursor: pointer;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 1.2rem;
  transition: var(--transition);
  background-color: transparent;
  backdrop-filter: blur(5px);
}

.header.scrolled .theme-toggle-btn {
  border-color: var(--primary-color);
  color: var(--primary-color);
}

[data-theme="dark"] .header .theme-toggle-btn {
  border-color: #ffffff;
  color: #ffffff;
}

.theme-toggle-btn:hover {
  background-color: var(--accent-color);
  color: var(--primary-color);
  transform: scale(1.1);
}

.header.scrolled .theme-toggle-btn:hover {
  background-color: var(--primary-color);
  color: var(--accent-color);
}

[data-theme="dark"] .header .theme-toggle-btn:hover {
  background-color: #ffffff;
  color: #000000;
}

.theme-toggle-btn i {
  transition: var(--transition);
}

.theme-toggle-btn:hover i {
  transform: rotate(180deg);
}

@media (max-width: 768px) {
  .nav-menu ul {
    gap: 15px;
  }
  
  .nav-menu button {
    font-size: 0.8rem;
  }
  
  .theme-toggle {
    margin-left: 15px;
  }
  
  .theme-toggle-btn {
    width: 40px;
    height: 40px;
    font-size: 1rem;
  }
}
